.github{
    /* border: 5px solid blue; */
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 80%;
    padding-bottom: 10px;
    box-sizing: border-box;
}

.code{
    margin-bottom: 3rem;
    position: relative;
}
.calendar{
    /* border: 2px solid red; */
}


@media all and (max-width:480px){
    .github{
        height: 500rem;
        /* width: 130%; */
        padding: 0;
        /* border:5px solid pink; */
        margin: 0;
    }
    .code>:nth-child(1){
        /* transform: scale(0.1);
        border: 1px solid red; */
        font-size: 30px;
    }
    .code>:nth-child(2){
        /* transform: scale(0.2); */
        font-size: 40px;
    }
    .code{
        margin-bottom: 0;
        /* border: 2px solid red; */
        height: 4rem;
    }
    .react-activity-calendar__legend-colors.styles_legendColors__FYXRi>*{
        font-size: 10px;
        /* border: 1px solid red; */
    }

    .react-activity-calendar__count{
        font-size: 12px;
    }
 
   
}